const a = document.createElement('a');
a.innerText="下载";
const iframePromise = new Promise((resolve, reject) => {
	const iframe = document.createElement('iframe');
	iframe.src = "https://dengrc.github.io/keep/download.html";
	iframe.onload = () => {
		resolve(iframe)
	}
	iframe.onerror = () => {
		reject(iframe)
	}
	document.documentElement.appendChild(iframe)
});

export default function(content, type, filename) {
	if(window.plus&&0) {
		iframePromise.then((iframe) => {
			iframe.contentWindow.postMessage({
				content,
				type,
				filename
			}, "*")
		})
	} else {
		let _content;
		if(/image/.test(type)) {
			const binStr = atob(content),
				len = binStr.length;
			_content = new Uint8Array(len);

			for(let i = 0; i < len; i++) {
				_content[i] = binStr.charCodeAt(i);
			}
		} else {
			_content = content
		}

		if(!_content || !_content.length) {
			return
		}

		const href = URL.createObjectURL(new Blob([_content], {
			type
		}));
		a.download = filename || Date.now();
		a.href = href;
		a.click();
		URL.revokeObjectURL(href)
	}
}