const validates = {
	required: function(v) {
		return v.toString().trim().length > 0 ? true : "必填项"
	},
	tel: function(v) {
		return /^1\d{10}$/.test(v) ? true : "格式错误"
	},
	gtZero: function(v) {
		return v > 0 ? true : "应大于 0"
	},
	gtOrEqualZero: function(v) {
		return v >= 0 ? true : "应大于等于 0"
	}
};
export default validates